.wp-block-b-blocks-cards .bBlocksCards {
	display: grid;

	* {
		box-sizing: border-box;
	}

	&.vertical {
		margin-bottom: 30px;

		.bBlocksCard {
			flex-direction: column;

			&.topImg {
				margin-top: 30px;

				.cardImage {
					margin-top: -30px;
					margin-bottom: 0;
				}
			}

			&.bottomImg {
				margin-bottom: 30px;

				.cardImage {
					margin-top: auto;
					margin-bottom: -30px;
				}
			}
		}
	}

	&.horizontal {
		.bBlocksCard {
			flex-direction: row;
			align-items: center;

			.cardDetails {
				width: 50%;
			}

			.cardImage {
				height: calc(100% - 30px);
				width: 50%;

				img {
					min-height: 100%;
				}
			}

			&.topImg {
				margin-left: 30px;

				.cardImage {
					margin-left: -30px;
				}
			}

			&.bottomImg {
				margin-right: 30px;

				.cardImage {
					margin-left: auto;
					margin-right: -30px;
				}
			}
		}
	}

	.bBlocksCard {
		height: 100%;
		display: flex;
		position: relative;
		border-radius: 5px;
		color: rgba(0, 0, 0, 0.87);
		box-shadow: 0 2px 2px 0 rgba(0, 0, 0, .14), 0 3px 1px -2px rgba(0, 0, 0, .2), 0 1px 5px 0 rgba(0, 0, 0, .12);

		.cardImage {
			position: relative;
			overflow: hidden;
			margin: 15px;
			box-shadow: 0 16px 38px -12px rgba(0, 0, 0, .56), 0 4px 25px 0px rgba(0, 0, 0, .12), 0 8px 10px -5px rgba(0, 0, 0, .2);

			img {
				width: 100%;
				object-fit: cover;
			}

			.cardTitle {
				position: absolute;
				left: 20px;
				bottom: 15px;
				text-shadow: 0 2px 5px rgba(33, 33, 33, .5);
				margin-bottom: 0 !important;
			}
		}

		.cardDetails * {
			margin: 0 0 10px 0;
		}

		.cardTitle {
			font-weight: 600;
			margin: 0;
		}

		.cardDescription {
			margin: 0;
		}
	}
}